body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: auto;
  min-height: 100vh;
  background-color: #E74646;
  max-width: 100%;
  

  
}

.cat-gallery {
  max-width: 50%;
  max-height: 70vh;
  padding: 20px;
  text-align: center;
  background-color: #d9d9d9;
  border-radius: 0px 0px 10px 10px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  
  
  
}

.cat-gallery img {
  max-width: 100%;  
  height: auto;
  max-height: 60vh;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
}

/* CELULAR */

@media (max-width: 768px) {
  .cat-gallery {
    max-width: 90%;
    max-height: 90vh;
    padding: 20px;
    text-align: center;
    background-color: #d9d9d9;
    border-radius: 0px 0px 10px 10px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    
    
    
  }
}
.header {
    display: flex;
    justify-content: space-between;
    padding: 0px;    
    width: 99%;
    background-color: #E74646;
    border-bottom: 1px solid #ddd;
    margin: 0px;
  }

  .header,p {
    color: #ddd;
    font-size: 30px;
    margin: 0px;
    padding: 5px;
  }
  
  .header-item {
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 5px 10px;
    border-radius: 5px;
    transition: background-color 0.2s ease;
  }
  
  .header-item:hover {
    
    -webkit-transform: scale(1.1);
    
            transform: scale(1.1); /* Aumenta o tamanho do botão em 10% quando clicado */
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out; /* Animação suave para o tamanho do botão */
  }
  
  .header-item img {
    margin-right: 10px;
    height: 50px;
  }
  
  .header-item.active {
    /* background-color: #ddd; */
  }
  
.cat-actions {
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
    flex: 1 1;
  }
  
  .btn-dislike,
  .btn-like {
    width: 50px;
    height: 50px;
    border-radius: 25px;  /* Bordas arredondadas */
    border: 2px solid;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    margin: 10px;
  }
  
  .btn-dislike img,
  .btn-like img {
    width: 100%;
    height: auto;
  }
  
  .btn-dislike.clicked,
.btn-like.clicked {
  -webkit-transform: scale(1.1);
          transform: scale(1.1); /* Aumenta o tamanho do botão em 10% quando clicado */
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out; /* Animação suave para o tamanho do botão */
}

.btn-dislike.clicked img,
.btn-like.clicked img {
  opacity: 0.7;  /* Diminui a opacidade da imagem quando o botão é clicado */
}
 
  
.saved-cats-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1 1;
  }
  
  .saved-cat-card {
    position: relative;
    margin-bottom: 20px;
    background-color: #d9d9d9;
    padding: 15px;
    border-radius: 15px;
  }
  
  .remove-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: red;
    color: white;
    border: none;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    font-size: 1em;
    cursor: pointer;
  }
  
  .remove-btn:focus {
    outline: none;
  }
  
  .saved-cat-card img {
    max-width: 100%;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .no-saved-cats {
  
    height: 100%;
    display: flex;
    text-align: center;
    justify-content: center;
    flex-direction: column;
    font-size: 30px;
    flex: 1 1; 
    
    
    
  }
  

.preloader-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  
  .spinner {
    border: 16px solid #f3f3f3;  /* Cor do fundo do círculo */
    border-top: 16px solid #3498db;  /* Cor do spinner */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
  }
  
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
